body {
    background-color: var(--base-color);
}

#header {
    background-color: var(--bar-color);
    
    height: var(--header-height);
    width: var(--header-width);

    margin: var(--header-margin) var(--header-margin);
    padding: 0 var(--header-padding);
    border-radius: var(--header-radius);

    display: flex;
    align-items: center;
    justify-content: space-between;

    /* overflow: hidden; */

    position: sticky;
    top: 0px;
    z-index: 2;
}

#header-logo {
    background-color: var(--bar-color);
    display: flex;

    /* width: 25vw; */

    margin-left: 10px;
}

#header-logo-text {
    color: var(--box-color);

    font-weight: 800;
    font-size: 2rem;
}

#user-in-header {
    height: 100%;
}