:root{
    /* Colors */

    /* PINK */
        /* DAY */
        --title-color: #FF4D6D;
        --err-color: #fb133a;
        --base-color: #fff;
        --toggle-base-color: #847f7f;
        --in-panel-color: #f2e1e4;
        --bar-color: #ff4d6e28; /* Transparent */
        --in-bar-color: #FFCCD5;
        --box-color: #ff4d6eb7; /* Transparent */
        --box-color-t0: #FF7F97; /* Transparent */
        --in-box-color: #FF4D6D;
        --in-box-color-selected: #C9184A;
        
        /* NIGHT */
        --dark-title-color: #FF4D6D;
        --dark-base-color: #000000; 
        --dark-bar-color: #9f39514d; 
        --dark-in-bar-color: #FFCCD5;
        --dark-box-color: #672736ae; 
        --dark-in-box-color: #FF4D6D;
        --dark-in-box-color-selected: #C9184A;

    /* PINK */

    --title-color: #FF4D6D;
    --err-color: #fb133a;
    --base-color: #f0f2f5;
    --light-base-color: #f0f2f5af;
    --bar-color: #fff; /* Transparent */
    --in-bar-color: #FFCCD5;
    --box-color: #ff4d6eb7; /* Transparent */
    --in-box-color: #FF4D6D;
    --in-box-color-selected: #C9184A;

    --fa-icon-color: var(--box-color);



    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/


    --short-margin: 5px;
    --long-margin: calc(var(--short-margin)*2);
    --short-padding: 5px;
    --long-padding: 10px;
    --short-radius: 5px;
    --long-panel-radius: 10px;
    --scroll-width: 5px;

    --x-small-font: 0.7rem;
    --small-font: 0.8rem;
    --normal-font: 1rem;
    --large-font: 1.2rem;
    --x-large-font: 2rem;

    --out-light-height: 1px;

    --transition-time: 300ms;
    --transition-time-fast: 100ms;
    --transition-style: ease-out;


    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
    
    
    /* Header */
    --header-margin: 1vh;
    --header-padding: 10px;
    --header-radius: var(--long-panel-radius);
    
    --header-height: 7vh;
    --header-width: calc( 100vw - var(--header-margin)*2 - var(--header-padding)*2 );
    
    --header-items-head-margin: calc( var(--header-height)/10 );
    
    --header-items-head-height: calc( var(--header-height) - (var(--header-items-head-margin)*2) - var(--line-height) );
    

    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
    

    /* SEARCH */
    /* --search-input-height: calc( var(--header-height) - (var(--header-items-head-margin)*2) - var(--line-height) ); */

    --search-curve-width: calc(var(--header-items-head-height)/2);

    
    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
    
    
    /* Panels */
    --panel-margin: 1vh;
    --panel-padding: 10px;
    --panel-radius: 10px;
    
    --panel-height: calc( 100vh - var(--header-height) - (var(--header-margin)*2) - (var(--panel-margin)*1) - (var(--panel-padding)*2) ); /* reducing margin and padding in refference to where they inicialise in their personal css */
    
    
    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/


    /* Side Panel */
    --acctual-side-panel-width: 25vw;

    --side-panel-width: calc( var(--acctual-side-panel-width) - var(--panel-margin)*2 - (var(--panel-padding)*2) );
    

    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/


    /* Top Show */
    --topshow-margin-right: 2vw;
    --topshow-margin-left: 1vw;
    

    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
    
    
    /* Feed */
    --feed-margin: 1vh;
    --feed-padding: var(--long-padding);
    --feed-radius: 10px;

    --feed-block-right-margin: 0.3vw;

    --feed-height: calc( 100vh - var(--header-height) - (var(--header-margin)*2) - (var(--feed-margin)*1));
    --feed-width: calc( 100vw - (var(--acctual-side-panel-width)*2) );

    --feed-block-height: calc( 30vh - (var(--feed-margin)*2) - (var(--feed-padding)*2) );
    /* --feed-block-height: calc( 30vh - (var(--feed-margin)*2)); */
    --feed-block-width: calc( 100% - (var(--feed-padding)*2) - var(--feed-block-right-margin) );


    --id-margin: calc(var(--short-margin));
    
    --id-height: calc((0.15 * var(--feed-block-height)) - var(--id-margin)); /* 20%(20/100) of feed-block-height */



    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/


    /* COMMENT */
    --comment-padding: calc(var(--id-height) + var(--short-margin));
    --min-comment-width: 70%;
    

    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/


    /* Changer */
    --changer-radius: var(--long-panel-radius);

    --changer-container-width: calc( var(--feed-width) - var(--feed-block-right-margin) - var(--scroll-width) );
    
    --changer-height: 7vh;
    --redu-width: 75;
    --changer-width: calc( (var(--redu-width)/100) * var(--feed-width) );

    --changer-bottom-gap: 10px;
    --changer-left-push: calc( (var(--feed-width) - var(--changer-width)) / 2 );
    /* --changer-left-gap: calc( var(--acctual-side-panel-width) + var(--changer-left-push) ); */
    --changer-left-gap: calc( var(--acctual-side-panel-width) );

    --changer-blocks-margin-x: 3px;
    --changer-blocks-margin-y: 10px;
    --changer-blocks-padding-x: 0px;
    --changer-blocks-padding-y: 10px;
    --changer-blocks-radius: var(--long-panel-radius);
    
    --changer-blocks-height: calc( var(--changer-height) - var(--changer-blocks-margin-x)*2 - var(--changer-blocks-padding-x)*2 );

    --changer-blocks-width: calc( (var(--changer-blocks-height)) - (var(--changer-blocks-padding-y)*2) ); /* No margin reduced because it's outside the width of box, although part of box but unseen part */
    --changer-block-size-multiplier: 3;
    --changer-blocks-hover-width: calc( var(--changer-blocks-height) * var(--changer-block-size-multiplier) );


    /* Ratio Design */
    /* Finding Changer Blocks Width */
        /* Inicilising Variables */
        /* --n: 3; */
        /* --m: 20px; */
        /* --twm: calc( var(--changer-width) - ( (var(--n) + 1) * var(--m) ) ); */

        /* --t: 10; to vary */
        /* --big: 5; to vary */
        /* --small: calc( var(--t) - var(--big) ); */
        /* --small-per-unit: calc( var(--small) / (var(--n) - 1) ); */

        /* Setting main width */
        /* --small-w: calc((var(--small-per-unit) / var(--t)) * var(--twm)); */
        /* --big-w: calc((var(--big) / var(--t)) * var(--twm)); */

    /* --changer-blocks-width: calc( var(--small-w) - (var(--changer-blocks-margin-y)*2) - (var(--changer-blocks-padding)*2) ); */
    /* --changer-blocks-hover-width: var(--big-w); */


    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/

    /* Auth */
    --auth-width: 25%;
    /* --auth-height: fit- content; */
    --auth-persumed-height: 40vh;

    --auth-border-radius: 15px;

    --auth-input-cont-width: 60%;
    --auth-input-cont-half-gap: 4%;
    --auth-input-cont-left: calc( (100% - var(--auth-input-cont-width)) / 2 );
    --auth-input-cont-margin-y: 3.5vh;

    --auth-input-height: 3.5vh;
    --auth-input-width: calc(100% - (var(--auth-input-radius)*2));
    --auth-input-margin-y: 0vh;

    /* --auth-input-radius: calc(var(--auth-input-height) / 2); */
    --auth-input-radius: var(--short-radius);

    /* --auth-input-icon-left: calc( var(--auth-input-width) - 1rem ); */

    --auth-input-cont-height: calc( var(--auth-input-height) + (var(--auth-input-margin-y)*2) );

    --auth-btn-width-percent: 80%;
    --auth-btn-width: calc(var(--auth-btn-width-percent) - (var(--short-padding)*2));


    /* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/


    /* Line */
    --line-height: 0.5vh;


    /* Button */
    --btn-height: 2.5vw;
}